les I've made: a template that implements the "),_c('b-link',{attrs:{"href":"https://en.wikipedia.org/wiki/Cayley%E2%80%93Dickson_construction"}},[_vm._v("Cayley-Dickson construction")]),_vm._v(" over the complex numbers and beyond (the template aliases itself to complex numbers at low enough recursion), a manual implementation of quaternions, and a wrapper around an arbitrary value that lets you set that value using a function called entirely in a separate thread. That last one was made when I was pretty naive with threading, so, go easy on it. ")],1),_c('work-entry',{attrs:{"name":"Auxmos","href":"https://github.com/Putnam3145/auxmos","blurb":"Multithreaded atmospherics plugin for BYOND"}},[_vm._v(" A total rewrite of Space Station 13's atmospherics in Rust. Multithreaded, with a lot of experiments thereof in the branches. Most of my time with it has been spent on algorithmic improvements. It performs very well, but the interface with BYOND causes a lot of grief; problems with improper refcounting on the byond side lead to nights of frustration. However, overall, the project is very fun for me, and, while some of the more monolithic functions in it horrify me, I still enjoy programming it and learning new things in the process. I even wrote my own unsafe lock-free vector, which primarily works by sharding and, of course, just panicking if it might ever need to move memory, since you just can't do that without a lock. ")]),_c('work-entry',{attrs:{"name":"DF material helper","href":"#","blurb":"Educational modding tool"}},[_vm._v(" I love Dwarf Fortress, and it's sparked a mild enthusiasm in me of many subjects. This is the product of one such minor enthusiasm. This is a little online widget that helps guide you through the creation of a modded material in Dwarf Fortress, providing the physical and gameplay ramifications of various material properties. Perhaps my favorite part is the elasticity page, which uses various real properties of materials Dwarf Fortress itself doesn't quite get to, and includes an embed to an interactive graph showing how the poisson ratio can be seen as a constrained proportionality constant between the elastic moduli in isotropic homogenous materials. ")])],1)],1),_c('b-col',[_c('b-card',[_c('b-card-title',[_vm._v(" Dwarf Fortress mods ")]),_c('b-card-sub-title',[_vm._v(" How I got started. ")]),_c('work-entry',{attrs:{"name":"Fortbent","href":"http://www.bay12forums.com/smf/index.php?topic=89267.0","blurb":"Homestuck mod, oh no!"}},[_vm._v(" My first ever programming project, unfortunately. Also grew outside of scope, so hard that unlike the DBZ mod I no longer find myself able to support it. Features a new relationship system built by hacking on top of Dwarf Fortress's, a new in-game semi-economy based on goings-on elsewhere in your fortress's civilization, 144 hand-coded class/aspect combos (this was a mistake), procedurally generated combination attacks based on each of those (also a mistake), and an entire custom skills system built to support this, all in Lua. One may figure out why I said I dislike Lua. ")]),_c('work-entry',{attrs:{"name":"SCP","href":"http://www.bay12forums.com/smf/index.php?topic=105645.0","blurb":"SCP wiki mod"}},[_vm._v(" Less a mod and more a series of tech demos. Demonstration of Dwarf Fortress's unused multiple-souls vector with Dr. Bright; the robustness of Dwarf Fortress's materials system with SCP-294; demonstration of how complicated behavior can be replicated with mere interactions with SCP-173; and so on and so forth. Had the beginnings of a more \"management-sim\" like system in the form of \"trust points\" that can be gained by successfully containing SCPs and spent on equipment, safer SCPs etc., but I eventually decided that if I'm going to make an SCP management sim, I'd be better of making it standalone. I haven't, primarily because the idea doesn't actually interest me that much. ")]),_c('work-entry',{attrs:{"name":"Sparking","href":"http://www.bay12forums.com/smf/index.php?topic=100799.0","blurb":"Dragon Ball mod, what?"}},[_vm._v(" A Dragon Ball Z mod that I started when I was 17 and got waaaaaay beyond the originally planned scope. Has a fully-featured transformations system built on top of another fully-featured ki system, both written in Lua using DFHack. One may notice a pattern. The main reason this has lasted so long for me is specifically because of how much I enjoy implementing new ideas and techniques I've learned about: for a very long time, whenever I would think up a new way to interact with Dwarf Fortress, this mod is where I would do it. The overall effect is a mod that is terrifyingly fully-featured and thought-out for me; it doesn't help that I actually play it. ")]),_c('work-entry',{attrs:{"name":"Fantastic","href":"http://www.bay12forums.com/smf/index.php?topic=150752.0","blurb":"Mini-mods"}},[_vm._v(" I just throw whatever minor things I make into this mod pack whenever I make them. Primary feature is Materials+, which used an older version of the above material helper to implement nearly every metal on the periodic table, with reasonably accurate features. Bit of a silly mod: molybdenum short swords abound. Depending on how the steam release goes, I'm likely to break this into many smaller releases. ")])],1)],1),_c('b-col',[_c('b-card',[_c('b-card-title',[_vm._v(" Contributions ")]),_c('b-card-sub-title',[_vm._v(" Contributing is as important as creating ")]),_c('b-modal',{attrs:{"id":"cit_warn","body-bg-variant":"dark","body-text-variant":"light","header-border-variant":"dark","footer-border-variant":"dark","footer-bg-variant":"warning","header-bg-variant":"dark","header-text-variant":"light"},scopedSlots:_vm._u([{key:"modal-footer",fn:function(){return [_c('b-button',{attrs:{"variant":"success"},on:{"click":function($event){return _vm.$bvModal.hide('cit_warn')}}},[_vm._v("No")]),_c('b-button',{attrs:{"variant":"danger","href":"https://github.com/Citadel-Station-13/Citadel-Station-13"}},[_vm._v(